class BigPicture extends React.Component {
    constructor(props) {
        super(props);
        
        this.onWidthChange = this.onWidthChange.bind(this);
        this.state = { active: true, height: 0 };
    }
    
    componentWillReceiveProps(newProps) {
        if (typeof newProps.active === 'boolean')
            this.setState({ active: newProps.active });
    }
    
    componentDidMount() {
        window.addEventListener('resize', this.onWidthChange);
        this.onWidthChange();
    }
    
    componentWillUnmount() {
        window.removeEventListener('resize', this.onWidthChange);
    }
    
    onWidthChange() {
        const ratio = 800 / 1920;
        
        let width = window.innerWidth,
            height = (width * ratio);
        
        this.setState({ height: height });
    }

    render() {
        let active = this.state.active,
            picture = <img 
                        className={ "BigPicture " + (active ? 'active' : 'inactive') }
                        src={ this.props.src }
                        alt={ this.props.alt }
                      />
    
        if (this.props.wrapper) {
            return (
                <div className="relativeWrapper" style={ {height: this.state.height} }>
                    { picture }
                </div>
            );
        } else {
            return picture;
        }

    }
    
}

