const getPictures = {
  getVaria: function(id) { 
      let picture = assets.pictures.varia[id];
      return { src: assets.paths.varia + picture.name, alt: picture.alt };
  },
  getRef: (id) => { 
      return { src: assets.paths.referenzen + assets.pictures.referenzen[id] };
  },
  getRefs: () => {
      let path = assets.paths.referenzen;                                                                      
      return assets.pictures.referenzen.map((ref) => path + ref);
  },
  getLogo: () => assets.paths.logo,
  getIcon: (name) => { 
      let icon = assets.pictures.icons[name];
      return { src: assets.paths.icons + icon.name, alt: icon.alt };
  },
  getSlides: () => assets.pictures.varia.map((picture) => ({ src: (assets.paths.varia + picture.name), alt: picture.alt }))
}

