// VARIABLES FOR GALLERY
let picturesListenedTo = [];

class GalleryImage extends React.Component { 
    constructor(props) {
        super(props);
        
        this.onImageLoad = this.onImageLoad.bind(this);
        this.handleClick = this.handleClick.bind(this);
        //this.checkVisibility = this.checkVisibility.bind(this);
        
        this.state = {
            width: null,
            height: null
        }
    }
    
    componentDidMount() {
        picturesListenedTo.push({ ref: this.imgRef, src: this.props.src });
    }
    
    onImageLoad() {
        const requiredSize = 250;
        
        let width = this.imgRef.offsetWidth,
            height = this.imgRef.offsetHeight,
            ratio = width / height;
        
        if (width < height) {
            width = requiredSize;
            height = width / ratio;
        } else if (height < width) {
            height = requiredSize;
            width = height * ratio;
        }
        
        this.setState({ width: width , height: height });
        this.imgRef.className += " loaded";
    }
    
    handleClick() {
        this.props.pictureClickHandler(this.props.src);
    }
    
    
    render() {
        const width = this.state.width;
        const height = this.state.height;
        
        return (
            <div className="galleryPictureWrapper" onClick={ this.handleClick }>
                <img 
                    className="galleryPicture"
                    alt="Click to open"
                    ref={ (imgRef) => this.imgRef = imgRef } 
                    onLoad= { this.onImageLoad }
                    style={ width ? { width: width + 'px', height: height + 'px' } : null }   
                />
                <div className="curtain"></div>
                <span className="plus">+</span>
                <Spinner />
            </div>
        );
    }
}

class Gallery extends React.Component {
    constructor(props) {
        super(props);
        
        this.pictureClickHandler = this.pictureClickHandler.bind(this);
        this.leftPicture = this.leftPicture.bind(this);
        this.rightPicture = this.rightPicture.bind(this);
        
        this.state = {
            activePictureSrc: null,
            activePictureIndex: null,
            picLoaded: false,
            wrapperResized: false
        }
        
        this.pictures = props.pictures;
        this.firstPicture = 0;
        this.lastPicture = (props.pictures.length - 1);
        this.pictureComponents = (
            <div key="Gallery1" className="gallery">
                {
                    this.pictures.map(
                        (src, index) => (
                            <GalleryImage key={ 'galleryImage' + index } src={ src } pictureClickHandler={ this.pictureClickHandler } />  
                        )
                    )
                }
            </div>
        );
    }
    
    checkVisibility() {  
        for(let pics = picturesListenedTo, i = 0, l = pics.length; i < l; i++) {
            let pic = pics[i],
                src = pic.src,
                ref = pic.ref,
                spotted = false;
            
            let windowHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight,
                windowTop = window.scrollY || document.documentElement.scrollTop || document.body.scrollTop,
                windowBottom = windowHeight + windowTop;
    
            let wrapper = ref.parentElement,
                imageTop =  wrapper.offsetTop, //wrapper.offsetTop,
                imageHeight = wrapper.clientHeight,
                imageBottom = imageTop + imageHeight;

            let inView = ((imageBottom >= windowTop) && (imageTop <= windowBottom));

            if (inView) {
                ref.src = src;
                spotted = true;
            } else if (spotted) {
                break;
            }
        }
    }
    
    componentDidMount() {
        window.scrollTo(0,0);
        window.addEventListener('scroll', this.checkVisibility);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.checkVisibility);
    }
    
    pictureClickHandler(activePictureSrc) {
        if (typeof activePictureSrc === 'string') {
            let activePictureIndex = this.pictures.indexOf(activePictureSrc);
            this.activatePicture(activePictureIndex);
        } else {
            this.deactivatePicture();
        }
    }
    
    deactivatePicture() {
        this.setState({ activePictureSrc: null, activePictureIndex: null, picLoaded: false, wrapperResized: false });
    }

    activatePicture(pictureIndex) {
        if (this.ActivePicture) {
            this.ActivePicture.className = "hidden";
            this.resizeWrapper(this.getPictureSize(this.ActivePicture));   
        } else {
            setTimeout(() => {
                this.setState({ wrapperResized: true });
            }, 500);
        }
        
        if (pictureIndex >= 0 && pictureIndex <= this.lastPicture) {
            let pictureSrc = this.pictures[pictureIndex];
            this.setState({ activePictureSrc: pictureSrc, activePictureIndex: pictureIndex });
        }
    }

    leftPicture() {
        let activePictureIndex = this.state.activePictureIndex - 1;
        
        if (activePictureIndex < 0) {
            activePictureIndex = this.lastPicture;
        }
        
        this.setState({ picLoaded: false, wrapperResized: false });
        this.activatePicture(activePictureIndex);        
    }

    rightPicture() {
        let activePictureIndex = this.state.activePictureIndex + 1;
        
        if (activePictureIndex > this.lastPicture) {
            activePictureIndex = 0;
        }
        
        this.setState({ picLoaded: false, wrapperResized: false });
        this.activatePicture(activePictureIndex);
    }

    resizeWrapper(size) {
        let wrapper = this.GalleryScreen;
        wrapper.style.width = size.x + 'px';
        wrapper.style.height = size.y + 'px';
        
        setTimeout(() => {
            this.setState({ wrapperResized: true });
        }, 500);
    }

    getPictureSize() {
        let picture = this.ActivePicture;
        if (picture) {
            return { x: picture.width, y: picture.height };    
        }
        return null;
    }

    render() {
        const allPictures = this.pictureComponents,
            browserActive = this.state.activePictureIndex != null;
        
        return (
            <div>
                { 
                    browserActive && (
                    <div>
                        <div className="galleryScreen" 
                        style={ { width: this.state.sizeX, height: this.state.sizeY } } 
                        ref={ (e) => this.GalleryScreen = e }>
                            <div 
                                className="closeGalleryScreen noSelect" 
                                onClick={ this.pictureClickHandler }><img src="/icons/close.svg" alt="Close modal"/></div>
                            <img 
                                src={ this.state.activePictureSrc } 
                                ref={ (e) => this.ActivePicture = e} 
                                onLoad={ () => this.setState({ picLoaded: true, sizeX: this.getPictureSize().x, sizeY: this.getPictureSize().y }) }
                                className={ this.state.wrapperResized ? "hidden loaded" : "hidden" }
                            />
                            <Spinner />
                            <div 
                                className="leftPicture noSelect" 
                                onClick={ this.leftPicture }><img src="/icons/arrow-left.svg" alt="View left photo" /></div>
                            <div 
                                className="rightPicture noSelect" 
                                onClick={ this.rightPicture }><img src="/icons/arrow-right.svg" alt="View right photo" /></div>
                        </div>
                        <div 
                            className="galleryCurtain" 
                            onClick={ this.pictureClickHandler }></div>
                    </div>
                    )
                }
                { allPictures }
            </div>
        )
        
    };
}

