function Input(props) {
    let input, isValid = null;
    
    if (props.type === "textarea") {
        input = <textarea name={ props.name } value={ props.value } onChange={ props.onChange }></textarea>;
    } else {
        input = <input name={ props.name } type={ props.type } value={ props.value } onChange={ props.onChange }/>;
    };
    
    const validity = props.validity;
  
    if (validity != null) {
        if (validity === true) {
            isValid = <span className="validInput"></span>;
        } else if(validity === false) {
            isValid = <span className="invalidInput"></span>;
        }
    }
    
    return (
        <label htmlFor={ props.name }>
            { input }
            { isValid }
            <span className={ props.value ? "formInputHint off" : "formInputHint" }>{ props.hint }</span>
        </label>
    );
}

function SendButton(props) {
    return (
        <button className="formSubmitButton" type="submit">{ props.text }</button>
    );
}

class Form extends React.Component {
    constructor(props) {
        super(props);
        
        this.handleChange = this.handleChange.bind(this);
        this.validateInputs = this.validateInputs.bind(this);
        this.state = {
            name: '',
            phone: '',
            email: '',
            message: '',
            validity: [null, null, null, null],
            messages: null
        }
    }
    
    handleChange(event) {
        const target = event.target;
        const inputName = target.name;
        const inputType = target.type;
        const inputValue = inputType === 'checkbox' ? target.checked : target.value;
   
        this.setState({ [inputName]: inputValue });
        this.validateInput(inputName, inputValue);
    }
    
    validators() { 
       return [
            /(.|\s)*\S(.|\s)*/,


            /^([\+][0-9]{1,3}[ \.\-])?([\(]{1}[0-9]{1,6}[\)])?([0-9 \.\-\/]{3,20})((x|ext|extension)[ ]?[0-9]{1,4})?$/, 
           
            /(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/,

            /(.|\s)*\S(.|\s)*/
        ];
    }
    
    validateInput(name, value) {
        const state = this.state;
        const validators = this.validators();
        
        let id = Object.keys(state).indexOf(name),
            validity = state.validity.slice();
        
        validity[id] = validators[id].test(value);
        
        this.setState({ validity: validity });
    }
    
    validateInputs(event) {
        const validators = this.validators();
        
        const required = [
            true,
            false,
            true,
            false
        ];
        
        const emptyMessages = [
            'Wie heißen Sie',
            'Telefonnummer eingeben',
            'Die E-Mail darf nicht leer sein',
            'Nachricht eingeben'
        ];
        
        const invalidMessages = [
            '', '', '', ''
        ];
        
        const properties = this.state;
        
        let i = 0,
            validityArray = [],
            validationMessages = [];
        
        for (let key in properties) {
            if (i < 4) {
                let input = document.querySelector('.contactForm').childNodes[i].firstChild,
                    valid = required[i] ? validators[i].test(properties[key]) : true;
                
                validityArray.push(valid);
                
                if (!valid) {
                    input.className = 'invalid';
                    validationMessages.push(emptyMessages[i]); 
                    event.preventDefault(); 
                } else {
                    input.className = '';
                }
            }
            i++;
        }
  
        this.setState({ validity: validityArray });
        
        if (event) {
            this.setState({ 
                messages: validationMessages.join(', ') 
            });
        }
        
        if (validationMessages.length < 1) {
            let formStatus = document.querySelector('.formStatus');
            formStatus.style.opacity = 1;
            setTimeout(() => {
                formStatus.style.opacity = 0;
                this.setState({ 
                    name: '',
                    phone: '',
                    email: '',
                    message: ''
                });
            }, 3000);
        }
    }
    
    render() {
        const state = this.state;
        
        return (
            <div className="contactFormWrapper">
                { state.messages && <div className="formMessages">{ state.messages }</div> }
            
                <form onSubmit={ this.validateInputs } className="contactForm" action={ this.props.action } method="POST" noValidate target="hidden-form">

                    <Input name="name" value={ state.name } onChange={ this.handleChange } hint="Vorname und Nachname" type="text" validity={ state.validity[0] }/>
                    <Input name="phone" value={ state.phone } onChange={ this.handleChange } hint="Telefonnummer" type="tel" validity={ state.validity[1] }/>
                    <Input name="email" value={ state.email } onChange={ this.handleChange } hint="E-Mail-Adresse
        " type="email" validity = { state.validity[2] } />
                    <Input name="message" value={ state.message } onChange={ this.handleChange } hint="Nachricht" type="textarea" validity={ state.validity[3] } />
                    <SendButton text="SENDEN" />
                </form>
                <iframe name="hidden-form" style={{ display: 'none'}}></iframe>
                <div className="formStatus">Formular empfangen.</div>
            </div>
        );
    }
}


