let lastScrollY = 0;
let goingDown = false;

class Header extends React.Component {
    constructor(props) {
        super(props);
        
        this.toggleMobileMenu = this.toggleMobileMenu.bind(this);
        this.state = {
            scrollingDown: false,
            mobileMenuOpen: false
        }
    }
    
    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }
    
    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }
    
    toggleMobileMenu() {
        this.setState((previousState) => {
            return { mobileMenuOpen: !previousState.mobileMenuOpen };
        });
    }
    
    Logo = React.createRef();
    
    handleScroll = () => {
        goingDown = lastScrollY && lastScrollY < window.scrollY;      
        lastScrollY = window.scrollY;
        

        if (goingDown && window.scrollY > 50) {
            this.setState({ scrollingDown: true });
        } else {
            this.setState({ scrollingDown: false });
        }
    }

    render() {
        return (
            <header>
                <nav className={ this.state.scrollingDown ? "navigation top" : "navigation" }>
                    <div className={ this.state.scrollingDown ? 'logoWrapper scaleLogo' : 'logoWrapper' } ref={ this.Logo }>
                        <NavLink to="/">
                            <img className="logo" src={ getPictures.getLogo() } alt="Logo" />
                        </NavLink>
                    </div>
                    <ul className={ "navigationLinks" + (this.state.mobileMenuOpen ? " active" : "")}>
                        {
                            _pages.map(
                                (page, index) => <li key={ 'NavLink' + index }><NavLink onClick={ this.toggleMobileMenu } to={ page.route } activeClassName="active">{ page.name }</NavLink></li>
                            )
                        }
                    </ul>
                    <Hamburger isActive={ this.state.mobileMenuOpen } onClick={ this.toggleMobileMenu } />
                </nav> 
            </header>
        );
    }
};

const Hamburger = (props) => {
    return (
        <div className={ (props.isActive ? "hamburger active" : "hamburger") } onClick={ props.onClick }>
            <div></div>
            <div></div>
            <div></div>
        </div>
    );
}

const Footer = () => (
    <footer className="footer">
        <div className="footerText">{ _footer.text }</div>
        <div className="footerLinks">
            {
                _footer.pages.map((page, index) => <NavLink key={ "FooterNavLink" + index } to={ page.route } activeClassName="active">{ page.name }</NavLink>)
            }
        </div>
    </footer>
);

const Main = () => (
    <Switch>
        {
            _pages.concat(_footer.pages).map(
                (page, index) => <Route exact key={ "Route" + index } path={ page.route } component={ page.component } />
            )
        }
    </Switch>
);

class ScrollToTop extends React.Component {
//    componentDidMount() {
//        window.onpageshow = function(event) {
////            if (event.persisted) {
////                window.location.reload() 
////            }
//            window.scrollTo(0, 0);
//        };
//    }
    

    constructor(props) {
        super(props);
 
        this.state = {
          loading: true
        };
    }
    
    load() {
        const preloader = this.preLoader;
        preloader.className = "preLoader active";
        setTimeout(
            function() {
                preloader.className = "preLoader";
        }, 500);
    }
    
    componentDidMount() {
        this.load();
    }
    
    componentWillUpdate() {
        this.load();
    }
    
    componentDidUpdate(prevProps) {
        window.scrollTo(0, 0);
    }
    
    render() {
        return <div ref={(e) => this.preLoader = e} className="preLoader active"><div className="lds-ring"><div></div><div></div><div></div><div></div></div></div>
    }
}

class App extends React.Component { 
    componentDidMount() {
        //window.onbeforeunload = () => window.scrollTo(0, 0);
    }
    
    render() {
        return (
            <div>
                <Header />
                <Main />
                <Footer />
                <ScrollTop />
            </div>
        );
    }
};

const ScrollTop = withRouter(ScrollToTop);


ReactDOM.render((
    <BrowserRouter>
        <App />
    </BrowserRouter>
), document.getElementById('root'));


